


// Some best-practice CSS that's useful for most apps
// Just remove them if they're not what you want
html {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}
.footer-div{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 42px;
    background-color: #fff;
    border-top: 1px solid #ddd;
}


